import React from "react";
import logoError from "../assets/error.jpg";

export default function Error404() {
    return (
        <div className="text-center mt-5">
            <h1>La Page que vous cherchez n'existe pas ! </h1>
            <br />
            <a href="/"><b> Retournez sur la page login !</b> </a>
            <div>
                <img src={logoError} width={200} height={200} title="Error 404 Page does not exist" alt="Error 404" />
            </div>
        </div>
    );
}
