import React, { useState, useContext } from "react";
import Modal from "react-modal";
import axios from "axios";
import "./login.css";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../context/Auth";

const baseURL = "http://localhost:5000";

export default function Login({ setLoged }) {
    const navigate = useNavigate();
    const [showModal, setShowModal] = useState(false);
    const [massege, setMassege] = useState("")
    const [user, setUser] = useState({ username: "", password: "" })
    const [isAuthenticated, setIsAuthenticated] = useContext(AuthContext);

    async function fetchData() {
        // console.log('entrer dans fn');
        try {
            const token = window.sessionStorage.getItem('miniblogToken');
            await axios.get('http://localhost:5000/home', {
                headers: {
                    'Authorization': `Safe ${token}`
                }
            }).then((res) => {
                console.log('res login :', res);
                setLoged(res.data.auth);
            });
        } catch (err) {
            console.error('error :', err);
            setLoged(false);
        }
    }
    // useEffect(() => {
    //     const token = window.sessionStorage.getItem('miniblogToken');
    //     console.log('token is here :', token);
    //     if (token) {
    //         // fetchData();
    //     }
    // });

    const handleChange = ({ currentTarget }) => {
        const { name, value } = currentTarget;
        setUser({ ...user, [name]: value })
    }

    function addItem(sessionName, newItem) {
        return window.sessionStorage.setItem(sessionName, newItem)
    }

    const onSubmit = async (e) => {
        e.preventDefault();
        await axios({ method: "post", baseURL: baseURL, url: "/login", data: { username: user.username, password: user.password } })
            .then((res) => {
                console.log("res data :", res.data);
                const { user, token } = res.data;
                addItem("miniblogToken", token);
                addItem("userName", user);
                fetchData();
                navigate("/home");
            })
            .catch(error => {
                if (error.response) {
                    console.log("here is response !", error.response.data)
                    setShowModal(true);
                    setMassege(error.response.data.message);
                } else {
                    console.log("Erreur inattendue :", error);
                    console.info(isAuthenticated);
                }
            });
    };


    return (
        <div className="page-login ">
            <div className="login">
                <Modal className="modal-signup rounded-pill bg-light col-5"
                    ariaHideApp={false} isOpen={showModal} onRequestClose={() => setShowModal(false)}>
                    <h1>{massege}</h1>
                    <button className="btn btn-secondary" onClick={() => setShowModal(false)}>Close</button>
                </Modal>
                <form onSubmit={onSubmit}>
                    <input className="login-input" required autoComplete="off" name="username" placeholder="Username ..." type="text" onChange={handleChange} />
                    <input className="login-input" required autoComplete="off" name="password" placeholder="Password ..." type="password" onChange={handleChange} />
                    <button type="submit" className="submit-button">Login</button>
                </form>
            </div>
        </div>
    )
}
